import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import home from '@/components/home'
import login from '@/components/login'
import forgetpw from '@/components/forgetpw'
import index from '@/components/index'
import productionList from '@/components/productionList'
import editProduction from '@/components/editProduction'
import createProduction from '@/components/createProduction'
import productionDetail from '@/components/productionDetail'
import characterList from '@/components/characterList'
import characterDetail from '@/components/characterDetail'
import editCharacter from '@/components/editCharacter'
import createCharacter from '@/components/createCharacter'
import dynamicList from '@/components/dynamicList'
import releaseDynamic from '@/components/releaseDynamic'
import QAList from '@/components/QAList'
import dynamicDetail from '@/components/dynamicDetail'
import replyDetail from '@/components/replyDetail'
import QADetail from '@/components/QADetail'
import QADetail2 from '@/components/QADetail2'
import fansList from '@/components/fansList'
import postingsDetail from '@/components/postingsDetail'
import postingsMore from '@/components/postingsMore'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      redirect: home
    },
    {
      path: '/home',
      name: 'home',
      component: home,
      meta: {
        isNeedlogin: false,//是否需要登录，true：需要登录，false：不需要登录
        isNeedNav: false,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    {
      path: '/login',
      name: 'login',
      component: login,
      meta: {
        isNeedlogin: false,
        isNeedNav: false,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    {
      path: '/forgetpw',
      name: 'forgetpw',
      component: forgetpw,
      meta: {
        isNeedlogin: false,
        isNeedNav: false,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    {
      path: '/index',
      name: 'index',
      component: index,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    {
      path: '/productionList',
      name: 'productionList',
      component: productionList,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
      // children: [
      //     {
      //       path: '', 
      //       component: productionList
      //     },
      //     {
      //       path: 'productionList', 
      //       name: 'productionList',
      //       component: productionList
      //     },
      //     {
      //       path: 'productionDetail', 
      //       name: 'productionDetail',
      //       component: productionDetail
      //     }
      // ]
    },
    {
      path: '/productionDetail',
      name: 'productionDetail',
      component: productionDetail,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    {
      path: '/editProduction',
      name: 'editProduction',
      component: editProduction,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    {
      path: '/createProduction',
      name: 'createProduction',
      component: createProduction,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    {
      path: '/characterList',
      name: 'characterList',
      component: characterList,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    {
      path: '/characterDetail',
      name: 'characterDetail',
      component: characterDetail,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    //编辑人物
    {
      path: '/editCharacter',
      name: 'editCharacter',
      component: editCharacter,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    {
      path: '/createCharacter',
      name: 'createCharacter',
      component: createCharacter,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    //动态列表
    {
      path: '/dynamicList',
      name: 'dynamicList',
      component: dynamicList,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    //发布动态
    {
      path: '/releaseDynamic',
      name: 'releaseDynamic',
      component: releaseDynamic,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    //问答列表
    {
      path: '/QAList',
      name: 'QAList',
      component: QAList,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    //动态详情
    {
      path: '/dynamicDetail',
      name: 'dynamicDetail',
      component: dynamicDetail,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    //回复详情
    {
      path: '/replyDetail',
      name: 'replyDetail',
      component: replyDetail,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    //问答详情
    {
      path: '/QADetail',
      name: 'QADetail',
      component: QADetail,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    //问答详情2
    {
      path: '/QADetail2',
      name: 'QADetail2',
      component: QADetail2,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    //后援会列表
    {
      path: '/fansList',
      name: 'fansList',
      component: fansList,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    //帖子详情
    {
      path: '/postingsDetail',
      name: 'postingsDetail',
      component: postingsDetail,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
    //更多帖子详情
    {
      path: '/postingsMore',
      name: 'postingsMore',
      component: postingsMore,
      meta: {
        isNeedlogin: true,
        isNeedNav: true,//是否需要显示pageHeader头、pageNav左导航，true：需要，false：不需要
      }
    },
  ],
  linkActiveClass: 'active',
  linkExactActiveClass: '',
})

export default router;

router.beforeEach((to, from, next)=>{
  //console.log(to);
  let isNeedlogin = to.meta.isNeedlogin;
  let path = to.name
  let adminId = sessionStorage.getItem('adminId');
  if(isNeedlogin){//如果是需要登录的页面，则判断是否在登录状态
      if(!adminId||adminId.length<1){//如果未登录
        //console.log(121);
        if(path == 'home') {
          next()
          return;
        }else{
          next({name: 'home'});
        }
      }else{
        next();
      }
  }else{
    next();
  }

})



